<template>
  <nav
    class="navbar fixed-top p-4" ref="qsNav"
    :style="{
      width: '100%',
    }"
  >
    <div class="row" style="width: 100vw" :style="cssProps">
      <!-- <div class="d-flex col-md-4 nav-item justify-content-start">
        <h1></h1>
      </div> -->
      <div
        class="
          d-flex
          col-md-4 col-sm-1 col-1
          nav-item
          justify-content-start
          align-items-start
        "
      >
        <h1>
          <router-link to="/"><img :src="qsLogo" /></router-link>
        </h1>
      </div>
      <div
        class="d-flex col-md-8 col-sm-11 col-11 p-4 pt-0 nav-item justify-content-end align-items-start"
      >
        <div class="row">
          <div @click="onToggleFilters()" class="btn btn-white m-0" v-if="showFilters">
            <h3 class="m-0">Filters &#x2191;</h3>
          </div>
          <div @click="onToggleFilters()" class="btn m-0" v-if="!showFilters">
            <h3 class="m-0">Filters &#x2193;</h3>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import Resize from "@/mixins/resize.js";

export default {
  name: "Navbar",
  props: ["name"],
  mixins: [Resize],
  data: function () {
    return {
      windowHeight: window.innerHeight,
      windowWidth: window.innerWidth,
      bNarrowScreen: false,
      qsLogo: require("@/assets/icons/logo.svg"),
      navHeight: 0,
    };
  },
  methods: {
    onToggleFilters() {
      this.$store.commit("toggle_filters");
    },
    onResizeNav() {
      this.navHeight = this.$refs.qsNav.clientHeight;
    }
  },
  watch: {
    navHeight: function(val) {
      this.$store.commit("set_navbar_height", val);
    }
  },
  computed: {
    cssProps: function () {
      return {
        "--width": "50px",
        "--pointerCursor": "url(" + window.pointerCursor + ") 6 6, pointer",
      };
    },
    showFilters: function () {
      return this.$store.state.showFilters;
    },
  },
  created() {
    window.addEventListener("resize", this.onResize);
    window.addEventListener("resize", this.onResizeNav);
  },
  mounted() {
    this.navHeight = this.$refs.qsNav.clientHeight;
  },
  unmounted() {
    window.removeEventListener("resize", this.onResize);
    window.removeEventListener("resize", this.onResizeNav);
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
// @import "./../assets/scss/_variables.scss";

a {
  color: rgb(255, 255, 255) !important;
  text-decoration: none !important;
}
.navbar {
  background-color: transparent;

  z-index: 60000 !important;
}
.nav-item {
  font-family: "Hatton";
  text-transform: uppercase;
}
.nav-item a:hover {
  /* text-decoration: underline; */
  /* border-bottom: 1px solid white;
    display: inline-block;
    line-height: 0.85; */
  color: rgb(255, 255, 255);
  cursor: var(--pointerCursor);
}
a router-link-active {
  text-decoration: none !important;
  color: rgb(255, 255, 255);
}

